import { useState } from 'react';
const { registerBlockType } = wp.blocks;

import SE2_container from '../components/container';

registerBlockType('se2block/section', {

     //biult-in attributes
     title: 'Section',
     description: 'aldsfjasldkfjaösdfj',
     icon: 'format-image',
     category: 'se2',

     //custom attributes
     attributes: {
          containerstyle: { type: 'object' }
     },

     //built-in functions
     edit({ attributes, setAttributes }) {

          function containerStyle(e) {
               setAttributes({ containerstyle: e });
               console.log('STYLE:', attributes);
          }

          return ([
               <SE2_container styleHandler={containerStyle}>
                    <h3>Margin: </h3>
               </SE2_container>
          ]);
     },

     save() {


     },

});

