import React, { Component, useState } from 'react';
import { __experimentalNumberControl as NumberControl } from '@wordpress/components';
const {
     RichText,
     InspectorControls,
} = wp.blockEditor;
const { PanelBody } = wp.components;

export default class SE2_container extends Component {

     constructor(props) {
          super(props)
          let defaultStyle = {
               margin: {
                    top: 50,
                    right: 0,
                    bottom: 0,
                    left: 0
               }
          }

          this.state = defaultStyle;
          this.sendStyleChanges = this.sendStyleChanges.bind(this)
          this.changeMargin = this.changeMargin.bind(this)

     }
     changeMargin = (event) => {
          this.setState(prevState => ({
               margin: { top: event.target.value }
          }), () =>
               this.sendStyleChanges(this.state)
          )
     }


     sendStyleChanges(style) {
          this.props.styleHandler(style)
     }

     render() {

          return ([
               <InspectorControls style={{ marginBottom: '40px' }}>
                    <PanelBody title={'Background Image Settings'}>

                         <p>Set margin of this Block:</p>
                         <NumberControl style={{ width: '50%' }}
                              onChange={(value) => { this.changeMargin }}
                              isDragEnabled
                              isShiftStepEnabled
                              shiftStep={1}
                              step={1}
                              value={this.state.margin.top}
                         />

                    </PanelBody>
                    <button onClick={this.sendStyleChanges}>CLICK</button>
               </InspectorControls>,

               <div className="se2-container" style={{
                    marginTop: `${this.state.margin.top}px`
               }}>

                    {this.props.children}
               </div>
          ])
     }
}